"use client";

import React, { useState, useRef } from "react";
import { Mic, MicOff, Volume2, Pause } from "lucide-react";

export default function TranslatorPage() {
  const [isRecording, setIsRecording] = useState(false);
  const [isTranslating, setIsTranslating] = useState(false);
  const [isPlaying, setIsPlaying] = useState(false);
  const [inputText, setInputText] = useState("");
  const [translatedText, setTranslatedText] = useState("");
  const [destLanguage, setDestLanguage] = useState("es"); // default to Spanish
  const [error, setError] = useState("");
  const [audioUrl, setAudioUrl] = useState(null); // to store the synthesized audio URL

  const mediaRecorderRef = useRef(null);
  const audioChunksRef = useRef([]);

  // Helper function to convert a Blob to base64
  const blobToBase64 = (blob) =>
    new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onloadend = () => resolve(reader.result.split(",")[1]);
      reader.onerror = reject;
      reader.readAsDataURL(blob);
    });

  // Start recording audio
  const startRecording = async () => {
    setError("");
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      mediaRecorderRef.current = new MediaRecorder(stream);
      audioChunksRef.current = [];

      mediaRecorderRef.current.ondataavailable = (event) => {
        if (event.data.size > 0) {
          audioChunksRef.current.push(event.data);
        }
      };

      mediaRecorderRef.current.onstop = handleAudioData;
      mediaRecorderRef.current.start();
      setIsRecording(true);
    } catch (err) {
      console.error("Error accessing microphone:", err);
      setError("Microphone access is required.");
    }
  };

  // Handle audio data when recording stops
  const handleAudioData = async () => {
    const audioBlob = new Blob(audioChunksRef.current, { type: "audio/wav" });
    try {
      const audioBase64 = await blobToBase64(audioBlob);
      // Post the base64 audio along with the destination language to your transcription backend
      postAudio(audioBase64);
    } catch (err) {
      console.error("Error converting audio:", err);
      setError("Error processing audio.");
      setIsTranslating(false);
    }
  };

  // Post base64 audio to the transcription backend route
  const postAudio = async (audioBase64) => {
    setIsTranslating(true);
    setError("");
    try {
      const response = await fetch("/api/transcription", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ audioFileBase64: audioBase64, destLanguage }),
      });
      if (!response.ok) {
        throw new Error("Transcription failed.");
      }
      const data = await response.json();
      // Assuming the response contains inputText and destinationTranscript/translatedText
      setInputText(data.inputText || "Audio received.");
      const transcript = data.destinationTranscript;
      setTranslatedText(transcript);

      if (transcript) {
        await playVoiceResponse(transcript);
      }
    } catch (err) {
      console.error(err);
      setError("Error during transcription.");
    } finally {
      setIsTranslating(false);
    }
  };

  const playVoiceResponse = async (text) => {
    setIsPlaying(true);
    try {
      const response = await fetch("/api/voice", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ transcript: text }),
      });
      if (!response.ok) {
        throw new Error("Voice synthesis failed.");
      }
      // Get the audio blob from the response
      const audioBlob = await response.blob();
      console.log("Audio blob size:", audioBlob.size);
      console.log("Audio blob type:", audioBlob.type);

      // Check if blob size is non-zero and the type is valid
      if (audioBlob.size === 0 || !audioBlob.type.includes("audio")) {
        throw new Error("Received empty or invalid audio blob");
      }

      // Create an object URL from the blob
      const url = URL.createObjectURL(audioBlob);
      setAudioUrl(url);
      console.log("Generated audio URL:", url);

      // Create an Audio element and play immediately.
      const audio = new Audio(url);
      audio.play().catch((err) => {
        console.error("Audio play error:", err);
      });
      audio.onended = () => {
        setIsPlaying(false);
        URL.revokeObjectURL(url);
        setAudioUrl(null);
      };
    } catch (err) {
      console.error(err);
      setError("Error during voice playback.");
      setIsPlaying(false);
    }
  };

  // Stop recording audio
  const stopRecording = () => {
    if (
      mediaRecorderRef.current &&
      mediaRecorderRef.current.state !== "inactive"
    ) {
      mediaRecorderRef.current.stop();
      setIsRecording(false);
      // Stop all active tracks
      mediaRecorderRef.current.stream
        .getTracks()
        .forEach((track) => track.stop());
    }
  };

  return (
    <div className="flex flex-col min-h-screen bg-[#F0EBE3]">
      {/* Navbar */}
      <nav className="bg-[#E4DCCF] px-6 py-4 shadow-md">
        <div className="container mx-auto flex justify-between items-center">
          <h1 className="text-[#576F72] text-2xl font-bold">AI Translator</h1>
          <div className="flex space-x-4">
            <button className="text-[#576F72] hover:text-[#7D9D9C] px-4 py-2">
              About
            </button>
            <button className="text-[#576F72] hover:text-[#7D9D9C] px-4 py-2">
              Settings
            </button>
          </div>
        </div>
      </nav>

      {/* Main Content */}
      <main className="flex-grow container mx-auto py-12 px-4">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {/* Input Card */}
          <div className="bg-white border border-[#E4DCCF] rounded-lg shadow-lg overflow-hidden">
            <div className="bg-[#E4DCCF] border-b border-[#7D9D9C]/20 px-6 py-4">
              <h2 className="text-[#576F72] text-xl font-semibold">
                Speech Input
              </h2>
            </div>
            <div className="p-6">
              <div className="flex flex-col space-y-4">
                {/* Destination Language Selector */}
                <div className="flex items-center space-x-2">
                  <label className="text-[#576F72]">Target Language:</label>
                  <input
                    type="text"
                    value={destLanguage}
                    onChange={(e) => setDestLanguage(e.target.value)}
                    className="border rounded px-2 py-1"
                    placeholder="e.g., es, fr, de"
                  />
                </div>

                <div className="min-h-[150px] border p-4 rounded bg-gray-50">
                  {isRecording ? (
                    <div className="flex items-center justify-center">
                      <div className="relative">
                        <div className="w-24 h-24 rounded-full bg-[#7D9D9C]/20 flex items-center justify-center">
                          <Mic className="w-12 h-12 text-[#7D9D9C] animate-pulse" />
                        </div>
                        <div className="absolute top-0 left-0 w-full h-full rounded-full border-4 border-[#7D9D9C] animate-ping opacity-30"></div>
                      </div>
                    </div>
                  ) : (
                    <p className="text-[#576F72] whitespace-pre-wrap">
                      {inputText || "Your transcript will appear here."}
                    </p>
                  )}
                </div>

                <div className="text-center">
                  {isRecording ? (
                    <button
                      onClick={stopRecording}
                      className="bg-[#576F72] hover:bg-[#7D9D9C] text-white px-4 py-2 rounded-md flex items-center justify-center mx-auto"
                    >
                      <MicOff className="mr-2 h-5 w-5" />
                      Stop Recording
                    </button>
                  ) : (
                    <button
                      onClick={startRecording}
                      disabled={isTranslating || isPlaying}
                      className={`bg-[#7D9D9C] hover:bg-[#576F72] text-white px-4 py-2 rounded-md flex items-center justify-center mx-auto ${
                        isTranslating || isPlaying
                          ? "opacity-50 cursor-not-allowed"
                          : ""
                      }`}
                    >
                      <Mic className="mr-2 h-5 w-5" />
                      Start Recording
                    </button>
                  )}
                </div>
                {error && <p className="text-red-500 text-center">{error}</p>}
              </div>
            </div>
          </div>

          {/* Output Card */}
          <div className="bg-white border border-[#E4DCCF] rounded-lg shadow-lg overflow-hidden">
            <div className="bg-[#E4DCCF] border-b border-[#7D9D9C]/20 px-6 py-4">
              <h2 className="text-[#576F72] text-xl font-semibold">
                Translation Output
              </h2>
            </div>
            <div className="p-6">
              <div className="flex flex-col space-y-4 min-h-[150px]">
                <div className="border p-4 rounded bg-gray-50 flex-grow">
                  {isTranslating ? (
                    <div className="flex flex-col items-center justify-center h-full">
                      <div className="w-10 h-10 border-4 border-[#7D9D9C] border-t-transparent rounded-full animate-spin mb-2"></div>
                      <p className="text-[#576F72]">Translating...</p>
                    </div>
                  ) : (
                    <p className="text-[#576F72] whitespace-pre-wrap">
                      {translatedText ||
                        "Your translation will appear here, and then be read aloud."}
                    </p>
                  )}
                </div>

                <div className="text-center">
                  {isPlaying ? (
                    <button
                      onClick={() => setIsPlaying(false)}
                      className="bg-[#576F72] hover:bg-[#7D9D9C] text-white px-4 py-2 rounded-md flex items-center justify-center mx-auto"
                    >
                      <Pause className="mr-2 h-5 w-5" />
                      Stop Audio
                    </button>
                  ) : (
                    <button
                      onClick={() => playVoiceResponse(translatedText)}
                      disabled={
                        isTranslating ||
                        isRecording ||
                        !translatedText ||
                        isPlaying
                      }
                      className={`bg-[#7D9D9C] hover:bg-[#576F72] text-white px-4 py-2 rounded-md flex items-center justify-center mx-auto ${
                        isTranslating ||
                        isRecording ||
                        !translatedText ||
                        isPlaying
                          ? "opacity-50 cursor-not-allowed"
                          : ""
                      }`}
                    >
                      <Volume2 className="mr-2 h-5 w-5" />
                      Play Voice
                    </button>
                  )}
                </div>

                {/* If an audio URL exists, show an audio player with controls */}
                {audioUrl && (
                  <div className="mt-4">
                    <audio src={audioUrl} controls autoPlay />
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </main>

      {/* Footer */}
      <footer className="bg-[#E4DCCF] py-6 px-4">
        <div className="container mx-auto flex flex-col md:flex-row justify-between items-center">
          <p className="text-[#576F72] text-sm mb-2 md:mb-0">
            ©️ 2025 AI Translator. All rights reserved.
          </p>
          <div className="flex space-x-4">
            <button className="text-[#576F72] hover:text-[#7D9D9C]">
              Terms
            </button>
            <button className="text-[#576F72] hover:text-[#7D9D9C]">
              Privacy
            </button>
            <button className="text-[#576F72] hover:text-[#7D9D9C]">
              Contact
            </button>
          </div>
        </div>
      </footer>
    </div>
  );
}
